.productsContainer {
  padding: 24px;
  max-width: 1400px;
  margin: 0 auto;
}

.productsHeader {
  margin-bottom: 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
}

.productsTitle {
  font-size: 28px;
  font-weight: 600;
  margin: 0;
  color: var(--text-primary);
}

.btnPrimary {
  padding: 10px 16px;
  background: var(--primary-color);
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
  font-size: 14px;
  transition: opacity 0.2s;
}

.btnPrimary:hover {
  opacity: 0.9;
}

.btnSecondary {
  padding: 8px 12px;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  color: var(--text-primary);
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
  font-size: 13px;
  transition: background-color 0.2s;
}

.btnSecondary:hover {
  background: var(--bg-tertiary);
}

.btnDanger {
  padding: 8px 12px;
  background: #fee2e2;
  color: #991b1b;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
  font-size: 13px;
  transition: background-color 0.2s;
}

.btnDanger:hover {
  background: #fecaca;
}

body.theme-dark .btnDanger {
  background: #7f1d1d;
  color: #fecaca;
}

body.theme-dark .btnDanger:hover {
  background: #991b1b;
}

.productsLoading {
  text-align: center;
  padding: 48px 24px;
  color: var(--text-secondary);
  font-size: 16px;
}

.productsStats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 16px;
  margin-bottom: 32px;
}

.statCard {
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 16px;
  text-align: center;
}

.statLabel {
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: var(--text-secondary);
  margin-bottom: 8px;
}

.statValue {
  font-size: 28px;
  font-weight: 600;
  color: var(--text-primary);
}

.productsSection {
  margin-bottom: 32px;
}

.sectionTitle {
  font-size: 20px;
  font-weight: 600;
  margin: 0 0 24px 0;
  color: var(--text-primary);
}

.productsTableWrap {
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  overflow: auto;
}

.emptyState {
  padding: 48px 24px;
  text-align: center;
  color: var(--text-secondary);
}

.emptyState p {
  margin: 0;
  font-size: 16px;
}

.productsTable {
  width: 100%;
  border-collapse: collapse;
  font-size: 14px;
}

.productsTable thead {
  background: var(--bg-tertiary);
  border-bottom: 1px solid var(--border-color);
}

.productsTable th {
  padding: 12px 16px;
  text-align: left;
  font-weight: 600;
  color: var(--text-secondary);
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 0.5px;
}

.productsTable tbody tr {
  border-bottom: 1px solid var(--border-color);
  transition: background-color 0.2s;
}

.productsTable tbody tr:hover {
  background: var(--bg-hover);
}

.productsTable tbody tr:last-child {
  border-bottom: none;
}

.productsTable td {
  padding: 12px 16px;
  color: var(--text-primary);
  vertical-align: middle;
}

.productNameCell {
  display: flex;
  align-items: center;
  gap: 12px;
}

.productThumb {
  width: 40px;
  height: 40px;
  border-radius: 6px;
  object-fit: cover;
  background: var(--bg-tertiary);
}

.productName {
  font-weight: 500;
  color: var(--text-primary);
}

.productDesc {
  font-size: 12px;
  color: var(--text-secondary);
  margin-top: 4px;
}

.priceCell {
  font-weight: 600;
  color: var(--success-color);
}

.stockCell {
  text-align: center;
}

.stockBadge {
  display: inline-block;
  padding: 4px 8px;
  border-radius: 4px;
  background: var(--bg-tertiary);
  color: var(--text-primary);
  font-weight: 500;
  font-size: 12px;
}

.stockBadgeOutOfStock {
  background: #fee2e2;
  color: #991b1b;
}

body.theme-dark .stockBadgeOutOfStock {
  background: #7f1d1d;
  color: #fecaca;
}

.stockBadgeLowStock {
  background: #fef3c7;
  color: #92400e;
}

body.theme-dark .stockBadgeLowStock {
  background: #78350f;
  color: #fcd34d;
}

.statusBadge {
  display: inline-block;
  padding: 4px 12px;
  border-radius: 20px;
  font-weight: 500;
  font-size: 12px;
}

.statusBadgeAvailable {
  background: #dcfce7;
  color: #166534;
}

body.theme-dark .statusBadgeAvailable {
  background: #14532d;
  color: #86efac;
}

.statusBadgeUnavailable {
  background: #fee2e2;
  color: #991b1b;
}

body.theme-dark .statusBadgeUnavailable {
  background: #7f1d1d;
  color: #fecaca;
}

/* Form styling */
.formContainer {
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 24px;
  margin-bottom: 32px;
}

.formGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 16px;
  margin-bottom: 16px;
}

.formGroup {
  display: flex;
  flex-direction: column;
}

.formLabel {
  font-size: 14px;
  font-weight: 500;
  color: var(--text-secondary);
  margin-bottom: 6px;
}

.formInput,
.formTextarea,
.formSelect {
  padding: 10px 12px;
  border: 1px solid var(--border-color);
  border-radius: 6px;
  background: var(--bg-primary);
  color: var(--text-primary);
  font-size: 14px;
  transition: border-color 0.2s;
}

.formInput:focus,
.formTextarea:focus,
.formSelect:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(var(--primary-rgb), 0.1);
}

.formTextarea {
  resize: vertical;
  min-height: 100px;
}

.imagePreview {
  width: 100px;
  height: 100px;
  border-radius: 6px;
  background: var(--bg-tertiary);
  object-fit: cover;
}

.toggleSwitch {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}

.toggleInput {
  appearance: none;
  width: 48px;
  height: 24px;
  border-radius: 12px;
  background: #d1d5db;
  cursor: pointer;
  transition: background 0.2s;
  position: relative;
}

.toggleInput:checked {
  background: var(--primary-color);
}

.toggleInput::before {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: white;
  top: 2px;
  left: 2px;
  transition: left 0.2s;
}

.toggleInput:checked::before {
  left: 26px;
}

.formActions {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
}

@media (max-width: 768px) {
  .productsContainer {
    padding: 16px;
  }

  .productsHeader {
    flex-direction: column;
    align-items: flex-start;
  }

  .productsTitle {
    font-size: 24px;
  }

  .productsStats {
    grid-template-columns: 1fr 1fr;
  }

  .formGrid {
    grid-template-columns: 1fr;
  }

  .productsTableWrap {
    overflow-x: auto;
  }

  .productsTable th,
  .productsTable td {
    padding: 10px 12px;
    font-size: 13px;
  }

  .productThumb {
    width: 36px;
    height: 36px;
  }
}
